<template>
  <div>
    <div class="miniheader">
      <div class="container">
        <div class="miniheader-logo">
          <router-link to="/">小米官网</router-link>
        </div>
        <div class="miniheader-title">
          <h2>我的购物车</h2>
          <p>温馨提示：产品是否购买成功，以最终下单为准哦，请尽快结算</p>
        </div>
        <div class="miniheader-info">
          <a href="#" class="user-name">
            <span>{{user.name}}</span>
          </a>
          <span class="sep">|</span>
          <a href="javascript:;" class="user-name" @click="confirmQuit">
            <span>注销</span>
          </a>
          <!-- <a href="#" class="info-order">我的订单</a> -->
        </div>
      </div>
    </div>

    <div class="cartmain">
      <div class="container">
        <div class="cartmain-list">
          <div class="list-head clearfix">
            <div class="col col-check">
              <i
                class="iconfont icon-checkbox"
                :class="{select:selectArr.length===cartInfo.length}"
                @click="checkAll"
              >&#x221a;</i> 全选
            </div>
            <div class="col col-img">&nbsp;</div>
            <div class="col col-name">商品名称</div>
            <div class="col col-price">单价</div>
            <div class="col col-num">数量</div>
            <div class="col col-total">小计</div>
            <div class="col col-action">操作</div>
          </div>
          <div class="list-body">
            <div class="item-box clearfix" v-for="(item,index) in cartInfo" :key="index">
              <div class="col col-check">
                <i
                  class="iconfont icon-checkbox"
                  :class="{select:selectArr.includes(index)}"
                  @click="checkItem(index)"
                >&#x221a;</i>
              </div>
              <div class="col col-img">
                <a href="#">
                  <img :src="item.imgs[0]" alt>
                </a>
              </div>
              <div class="col col-name">{{item.name}}</div>
              <div class="col col-price">
                <span class="price">{{item.price}}</span>元
              </div>
              <div class="col col-num">
                <div class="change-num clearfix">
                  <a href="javascript:void(0)" @click="updateQuantity(item.id,-1,index)">
                    <i class="iconfont">&#xe60b;</i>
                  </a>
                  <input type="text" :value="item.quantity" :ref="`input_${index}`">
                  <a href="javascript:void(0)" @click="updateQuantity(item.id,1,index)">
                    <i class="iconfont">&#xe609;</i>
                  </a>
                </div>
              </div>
              <div class="col col-total">
                <span class="price">{{item.quantity*item.price}}</span>元
              </div>
              <div class="col col-action" @click="deleteItem(item.id)">
                <a href="javascript:void(0)">
                  <i class="iconfont">&#xe602;</i>
                </a>
              </div>
            </div>
          </div>
        </div>
        <div class="cartmain-bar clearfix">
          <div class="bar-info">
            <router-link to="/">继续购物</router-link>
            <span class="info-total">
              共
              <i class="total-num">{{cartInfo.length}}</i> 件商品，已选择
              <i class="total-num">{{selectTotal}}</i> 件
            </span>
          </div>
          <span class="total-price">
            合计：
            <em class="totalprice-num">{{selectTotalPrice}}</em>元
          </span>
          <form action="checkout.html" style="display: inline;" method="GET">
            <!--放在form里面可以发送ids数据-->
            <input type="hidden" id="ids" name="ids">
            <a
              href="javascript:;"
              id="checkout"
              class="bar-btn btn-primary"
              :class="{'btn-disabled':selectArr.length==0}"
              @click="gotoCheck"
            >去结算</a>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import api from "../../utils/api";
export default {
  created() {
    //   先登录
    // this.checklogin()
    //   .then(data => {
    //     console.log("现在是登录状态");
        api.get("/user/cart").then(data => {
          console.log(data);
          this.cartInfo = data;
        });
    //   })
    //   .catch(() => {
    //     console.log("现在是没登录状态");
    //   });
  },
  data() {
    return {
      cartInfo: {},
      selectArr: [],
      selectTotal: 0, //已选择商品件数
      selectTotalPrice: 0
    };
  },
  computed: {
    ...mapState("user", ["user"])
  },
  methods: {
    deleteItem(id) {
      api.post("/user/updateCart", { quantity: 0, id: id }).then(() => {
        let index = this.cartInfo.findIndex(el => el.id == id);
        this.cartInfo.splice(index, 1);
        if (this.selectArr.includes(index)) {
          this.selectArr.splice(this.selectArr.findIndex(el => el == index), 1); //删除selectArr中的item
          this.updateSum();
        }
        alert("删除成功");
      });
    },
    updateQuantity(id, num, index) {
      console.log(this.$refs);
      this.$refs["input_" + index][0].value =
        +this.$refs["input_" + index][0].value + num;
      if (num < 0 && this.$refs["input_" + index][0].value <= 1) {
        this.$refs["input_" + index][0].value = 1;
      }
      api
        .post("/user/updateCart", {
          quantity: this.$refs["input_" + index][0].value,
          id: id
        })
        .then(() => {});
    },
    checkItem(index) {
      if (this.selectArr.includes(index)) {
        let target = this.selectArr.findIndex(el => el === index);
        this.selectArr.splice(target, 1);
        // console.log(this.selectArr);
      } else {
        // console.log(this.selectArr);
        this.selectArr.push(index);
      }
      this.updateSum();
    },
    checkAll() {
      if (this.selectArr.length == this.cartInfo.length) {
        this.selectArr = [];
      } else {
        this.selectArr = this.cartInfo.map((el, idx) => idx);
      }
      this.updateSum();
    },
    updateSum() {
      this.selectTotal = this.selectArr.length;

      //计算总价钱
      if (this.selectArr.length > 0) {
        this.selectTotalPrice = this.selectArr.reduce((sum, cur) => {
          return (
            sum +
            parseInt(this.cartInfo[cur].quantity) *
              parseFloat(this.cartInfo[cur].price)
          );
        }, 0);
      } else {
        this.selectArr.length = 0;
      }
    },
    gotoCheck() {
      if (this.selectArr.length == 0) {
        alert("请选择需要结算的商品");
      } else {
        console.log(this.selectArr);
        const IDArr = this.selectArr.map(el => {
          return this.cartInfo[el].id;
        });
        console.log(IDArr);
        this.$router.push("/checkout/" + IDArr.join(","));
      }
    },
    confirmQuit() {
      this.$confirm("确认退出登录？")
        .then(() => {
          this.logout();
          this.$router.push("/login");
        })
        .catch(() => {});
    },
    ...mapActions("user", ["logout"])
  }
};
</script>

<style scoped>
@import url("../css/cart.css");
@import url("../css/iconfont.css");
@import url("../css/style.css");
</style>
